<template>
    <!---->
    <div class="form-group">
        <div class="form-tit mb8 d-flex">
            <span class="form-tit-line"></span>
            <h2></h2>
        </div>
        <!---->
        <div class="form-optbox pin" 
            v-for="(item,index) in radioData"
            :key="index"
            @click="showToggle(item)"
            :class="{'toggle': item.isChecked}" 
            >
            <!--+-->
            <div class="actions">
                <button type="button" class="btn btn-add mr5"
                @click="moveKey"
                >
                    <i class="fa jcm-trash6"></i>
                </button>
                <button type="button" class="btn btn-del"  
                @click="clickKey">
                    <i class="fa jcm-add6"></i>
                </button>
            </div>
            <div class="form-group d-flex align-items-center">
                <label class="control-label"></label>
                <div class="form-group-w">
                    <div class="input-group">
                        <input type="text" v-model="item.value" placeholder="" class="form-control">
                    </div>
                </div>
            </div>
            <div class="form-group d-flex align-items-center">
                <label class="control-label">value</label>
                <div class="form-group-w">
                    <div class="input-group">
                        <input type="text" v-model="item.key"  placeholder="value" class="form-control">
                    </div>
                </div>
            </div>
            <!--Checkbox-->
            <FormCheckbox></FormCheckbox>
            <!--Checkbox end-->
        </div>
        <!--end-->
    </div>
    <!--end-->
</template>
<script>
import FormCheckbox from './FormCheckbox'
export default {
    name: 'FormCheckbox2',
    components:{
        FormCheckbox,
    },
    data(){
        return {
            radioData: [
                {
                    value: '',
                    key:222,
                    isChecked: false
                }
            ]
        }
    },
    methods:{
        //html
        clickKey(){ 
            this.radioData.push( {
                value: '',
                key:111,
                isChecked: true
            })
        },
        //html
        moveKey(index){
            //html1HTML
            if(this.radioData.length > 1){
                this.radioData.splice(index,1);
            }
        },
        //
        showToggle(item){
            this.radioData.forEach((item) => {
            item.isChecked = true
            })
            this.$set(item, 'isChecked', false)
        }  
    }
}
</script>