<template>
    <div :class="$namespace +'-commented'">
        <!---->
        <div class="mycomment">
            <div class="avatar">
                <img src="@/assets/mobile/cu-pic2.jpg">
            </div>
            <div class="btn-mycomment">
                <a href="#" @click="openComment"></a>
            </div>
        </div>
        <!--end-->
        <!---->
        <ul class="comment-ul">
            <li v-for="(comment,index) in commentList" :key="index">
                <div class="avatar">
                    <img src="@/assets/mobile/cu-pic2.jpg">
                </div>
                <div class="comment-info">
                    <h2 class="name">{{comment.name}}</h2>
                    <p class="msg">{{comment.content}}</p>
                    <p class="tags">
                        <span class="time">{{comment.time}}</span>
                        <a class="btn-reply" href="#" @click="openReply(index)"></a>
                    </p>
                    <!---->
                    <div class="reply" v-if="comment.replyList&&comment.replyList.length>0">
                        <dt class="reply-dl">
                            <dl v-for="(reply,replyIndex) in comment.replyList" :key="replyIndex"
                                v-if="replyIndex<2||expandList[index]">
                                <span class="reply-name">{{reply.name}}</span>
                                <span>{{reply.content}}</span>
                            </dl>
                        </dt>
                        <div class="reply-all" v-if="comment.replyList.length>2&&!expandList[index]">
                            <a href="#" @click="expandedAllReply(index)">{{comment.replyList.length}}<i
                                    class="jcm-jiantou3"></i></a>
                        </div>
                    </div>
                    <!--end-->
                </div>
            </li>
        </ul>
        <!--end-->
        <div>
            <JCPopup v-model="popupVisible" position="bottom" style="width: 100%">
                <div>
                    <JCInput v-model="input" ref="commentedInput" type="textarea" :placeholder="placeholder"></JCInput>
                </div>
                <div style="float: right;padding: 10px">
                    <span class="jcm-smiley"></span>
                    <JCButton :type="'primary'" @click="createCommentOrReply"></JCButton>
                </div>
            </JCPopup>
        </div>
    </div>

</template>
<script>
    export default {
        name: "JCCommented",
        props: {
            value: {//Objectname:contenttimereplyList:[{name:content}]
                type: Array,
                default: () => []
            }
        },
        data() {
            return {
                placeholder: '',//
                input: '',//
                commentIndex: 0,//index
                popupVisible: false,//
                expandList: [],//
                commentList: this.value//
            }
        },
        watch: {
            commentList(val) {
                this.$emit('input', val)
                this.$emit('change', val)
            },
            popupVisible(val){
                if(val){
                    setTimeout(()=>{
                        this.$refs.commentedInput.focus();
                    },50)
                }
            }
        },
        methods: {
            openComment() {
                this.popupVisible = true;
                this.placeholder = '';
            },
            openReply(index) {
                this.commentIndex = index;
                this.popupVisible = true;
                this.placeholder = '';
            },
            createCommentOrReply() {
                this.popupVisible = false;
                if ('' == this.placeholder) {//
                    this.commentList.push({
                        name: '',
                        content: this.input,
                        time: this.formatDate(new Date().getTime())
                    })
                } else {//
                    var comment = this.commentList[this.commentIndex];
                    if (!comment.replyList)
                        comment.replyList = [];
                    comment.replyList.push({
                        name: '',
                        content: this.input
                    });
                }
                this.input = '';
            },
            expandedAllReply(index) {
                //this.expandList[index]=true,view
                this.$set(this.expandList, index, true);
            },
            formatDate(time, format = 'YY-MM-DD hh:mm:ss') {//
                var date = new Date(time);
                var year = date.getFullYear(),
                    month = date.getMonth() + 1,//0
                    day = date.getDate(),
                    hour = date.getHours(),
                    min = date.getMinutes(),
                    sec = date.getSeconds();
                var preArr = Array.apply(null, Array(10)).map(function (elem, index) {
                    return '0' + index;
                });////10  00 01 02 03
                var newTime = format.replace(/YY/g, year)
                    .replace(/MM/g, preArr[month] || month)
                    .replace(/DD/g, preArr[day] || day)
                    .replace(/hh/g, preArr[hour] || hour)
                    .replace(/mm/g, preArr[min] || min)
                    .replace(/ss/g, preArr[sec] || sec);
                return newTime;
            }
        }
    };
</script>

