<template>
  <!---->
    <div class="form-group d-flex">
        <label class="control-label"></label>
        <div class="form-group-w">
            <label class="form-group-label mb8"
             v-for="(item,index) in radioData"
             @click="select(item)"
             :class="{'selected': item.isChecked}" 
             :key="index"
             >
                <span class="ide-radio"></span>
                <span class="ide-radio-label">{{item.name}}</span> 
            </label>
            <div v-show="flag" class="input-group"><textarea required="required" rows="3" class="form-control" placeholder=":[ , ] "></textarea></div>
        </div>
    </div>
</template>
<script>
  export default {
    name: 'FormRadio2',
    data() {
      return {
        flag: true,
        radioData: [
          {
            name: '',
            isChecked: true
          },
          {
            name: '',
            isChecked: false
          }
          // {
          //   name: '',
          //   isChecked: false
          // }
        ]
      }
    },
    methods: {
      select(item) {
        this.radioData.forEach((item) => {
          item.isChecked = false
        })
        this.$set(item, 'isChecked', true)
        if (item.name === '') {
          this.flag = true
        } else {
          this.flag = false
        }
      }
    }
  }
</script>