/* eslint-disable */
/**
 * 
 */
class Property {
    constructor(args) {
		let _this = this;
		Object.keys(args).forEach((property) =>{
			if(!_this[property]){
				_this[property] = _this.copyObject(args[property]);
			}
		});
    }
    
    /**
	 * ,,
	 * 
	 * @param 	{any} 		obj  
	 * @memberof Property
	 */
	copyObject(obj){
		let item ,_this = this;
		switch(typeof obj){
			case 'string':
				item = String(obj);
			break;
			case 'boolean':
				item = Boolean(obj);
			break;
			case 'object':
				item = _this.deepCopy(obj)
			break;
			default:
			    item = item;
		}
		return item
	}
	/**
	 * or
	 * instanceofo instanceof Array  o instanceof Object
	 * instanceof Objecttrue
	 * 
	 * @param {any} o 
	 * @returns 
	 * @memberof Property
	 */
	deepCopy(o) {
		let _this = this;
		if (o instanceof Array) {
			let n = [];
			for (let i = 0; i < o.length; ++i) {
				n[i] = _this.deepCopy(o[i]);
			}
			return n;
 
		} else if (o instanceof Object) {
			let n = {}
			for (let i in o) {
				n[i] = _this.deepCopy(o[i]);
			}
			return n;
		} else {
			return o;
		}
	}
}


export default Property;